.home-block {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.home-block__section1 {
    height: 330px;
    margin-top: 15vh;
    display: flex;
    justify-content: center;
}

.home-block__text {
    height: 200px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    font-family: sans-serif;
    color: #ffffff;
}

.home-block__text--light-mode {
    color: #000000;
}

.home-block__download-cv {
    border: none;
    border-radius: 5px;
    background: #008eff;
    height: 40px;
    width: 120px;
    margin-top: 40px;
    z-index: 1;
    cursor: pointer;
}

.home-block__download-cv:active {
    padding: 2px 0 0 2px;
}

.themeMain {
    background: #ffffff;
    color: #000000;
    transition: background 0.3s linear;
}

.rectangles-block {
    position: absolute;
    top: -37px;
    left: -15px;
}

.rectangles-block__rec {
    position: absolute;
    border: 1px solid #ffffff79;
    width: 300px;
    height: 300px;
}

.rectangles-block__rec--light-mode {
    border-color: #0000008f;
}

.themeRec {
    border: 1px solid #00000079;
}

@keyframes rot1 {

    0%,
    30% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(-15deg);
    }
}

@keyframes rot2 {

    0%,
    30% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(-30deg) scale(0.8);
    }
}

@keyframes rot3 {

    0%,
    30% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(-45deg) scale(0.65);
    }
}

@keyframes rot4 {

    0%,
    30% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(-60deg) scale(0.53);
    }
}

@keyframes rot5 {

    0%,
    30% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(-75deg) scale(0.43);
    }
}

.rectangles-block__rect1 {
    animation: rot1 1.3s ease-in-out;
    transform: rotateZ(-15deg);
}

.rectangles-block__rect2 {
    animation: rot2 1.3s ease-in-out;
    transform: rotateZ(-30deg) scale(0.8);
}

.rectangles-block__rect3 {
    animation: rot3 1.3s ease-in-out;
    transform: rotateZ(-45deg) scale(0.65);
}

.rectangles-block__rect4 {
    animation: rot4 1.3s ease-in-out;
    transform: rotateZ(-60deg) scale(0.53);
}

.rectangles-block__rect5 {
    animation: rot5 1.3s ease-in-out;
    transform: rotateZ(-75deg) scale(0.43);
}

.home-block__section2 {
    background: #686868;
    width: 100%;
    height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #000000;
    font-family: sans-serif;
}

.home-block__section2--switch-theme {
    background: #00000021;
}

.home-block__skills-header {
    font-size: 25px;
    font-weight: bold;
}

.home-block__skills-text {
    width: 260px;
    margin-top: 15px;
    text-align: center;
    font-size: 14px;
}


